* {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
  }
body {
    font-family: "Segoe UI", Arial, sans-serif;
    line-height: 1.4;
    color: #1f1f1f;
}
.listContainer {
    width:100%;
    height:250px;
    text-align:center;
    margin-bottom: 20px;
    border-style: solid;
}
.ExportURLBox {
    display: inline-block;
    margin:0 auto;
    width: 33%;
    border-style: dotted;
    border-width: 1px;
}
.ResponseBox {
    float:right;
    border-style: dotted;
    width: 33%;
    border-width: 1px;
    overflow-y:scroll; overflow-x:hidden; height:100%;
} 
.InputBox {
    float:left;
    width: 66%;
    height: 100%;
    border-style: dotted;
    border-width: 1px;
}

/* Tooltip container */
.tooltip {
    position: relative;
    display: inline-block;
    border-bottom: 1px dotted black; /* If you want dots under the hoverable text */
  }
  
  /* Tooltip text */
  .tooltip .tooltiptext {
    visibility: hidden;
    width: 520px;
    background-color: #555;
    color: #fff;
    text-align: center;
    padding: 5px 0;
    border-radius: 6px;
  
    /* Position the tooltip text */
    position: absolute;
    z-index: 1;
    bottom: 125%;
    left: 50%;
    margin-left: -60px;
  
    /* Fade in tooltip */
    opacity: 0;
    transition: opacity 0.3s;
  }
  
  /* Tooltip arrow */
  .tooltip .tooltiptext::after {
    content: "";
    position: absolute;
    top: 100%;
    left: 50%;
    margin-left: -5px;
    border-width: 5px;
    border-style: solid;
    border-color: #555 transparent transparent transparent;
  }
  
  /* Show the tooltip text when you mouse over the tooltip container */
  .tooltip:hover .tooltiptext {
    visibility: visible;
    opacity: 1;
  }